import { Box, HStack, Input, InputGroup, InputLeftElement } from '@chakra-ui/react'
import Icon from './Icon'

export default function SearchInput(props: {
  searchText: string
  placeholder: string
  onChange: (text: string) => void
  h?: string
  iconMt?: string
  borderRadius?: string
  haveDeleteIcon?: boolean
}) {
  const { searchText, onChange, placeholder, h, iconMt, borderRadius, haveDeleteIcon = true } = props

  return (
    <HStack w="100%" justifyContent="space-between" position="relative">
      <InputGroup>
        <InputLeftElement justifyContent="center">
          <Icon mt={iconMt || '10px'} xlinkHref="#icon-icon_search" marginLeft="8px" />
        </InputLeftElement>
        <Input
          h={h || '40px'}
          pl="32px"
          pr="30px"
          placeholder={placeholder}
          variant="outline"
          fontSize="12px"
          borderRadius={borderRadius || 'full'}
          value={searchText}
          onChange={e => {
            onChange(e.target.value)
          }}
        />
      </InputGroup>
      {searchText && haveDeleteIcon ? (
        <Box position="absolute" top={iconMt || '10px'} right="8px" onClick={() => onChange('')}>
          <Icon xlinkHref="#icon-icon_close" />
        </Box>
      ) : null}
    </HStack>
  )
}
